// 
import Vue from "vue";
import Router from "vue-router";
import routes from "./routes";
// 
import {setTitle} from "@/untils";

//  Vue
if (!window.VueRouter) {
    Vue.use(Router);
}

const router = new Router({
    // 
    routes,
    // 
    mode: "hash"
});

router.afterEach((to) => {
    if (to.meta.title) {
        setTitle.setRouterTitle(to.meta.title);
    }   
})

export default router;