// 
/**
 * 
 * @param {String} refValue 
 * @returns 
 */
export default function(refValue) {
    return {
        mounted() {
            this.$refs[refValue].addEventListener("scroll", this.scrollTop);
            this.$Bus.$on("setScroll", this.setScrollTop);
          },
          methods: {
            scrollTop() {
              this.$Bus.$emit("handleScroll", this.$refs[refValue]);
            },
            setScrollTop(num) {
              this.$refs[refValue].scrollTop = num;
            },
          },
          beforeDestroy() {
            this.$refs[refValue].removeEventListener("scroll", this.scrollTop);
            this.$Bus.$off("setScroll", this.setScrollTop);
          },
    }
}