// 
// 
import instance from "./request";

// 
async function getBlogType() {
    const resp = await instance.get('/api/blogtype');
    return resp;
}

// 
async function getBlog(page = 1, limit = 10, categoryid = -1) {
    const resp = await instance.get('/api/blog', {
        params: {
            // 
            page,
            // 
            limit,
            // -1 
            categoryid
        }
    });
    return resp;
}

// 
/**
 *  id 
 * @param {String} id  Id
 */
async function getSingleBlog(id) {
    const resp = await instance.get(`/api/blog/${id}`);
    return resp;
}

// 
/**
 * {
	nickname: "",
	content: "",
	blogId: <id>	#id
}
 * @param {Object} info  
 * @returns 
 */
async function submitComment(info) {
    const resp = await instance.post('/api/comment', {
        params: info
    });
    return resp;
}

// 
/**
 * 
 * @param {Number} page 
 * @param {Number} limit 
 * @param {Number} blogid -1 
 * @returns 
 */
async function getComment(page = 1, limit = 10, blogid = -1) {
    const resp = await instance.get('/api/comment', {
        params: {
            page,
            limit,
            blogid
        }
    });
    return resp;
}

export {
    getBlogType,
    getBlog,
    getSingleBlog,
    submitComment,
    getComment
};