// &
import getProject from "@/api/api-project";

export default {
    namespaced: true,
    state: {
        loading: false,
        data: []
    },
    mutations: {
        // 
        setProjectStatus(state,payload) {
            state.loading = payload;
        },
        //  banner 
        setProject(state,payload) {
            state.data = payload;
        }
    },
    actions: {
        async getData(ctx) {
            if (ctx.state.data.length > 0) {
                return;
            }
            ctx.commit('setProjectStatus',true);
            const resp = await getProject();
            ctx.commit('setProjectStatus',false);
            ctx.commit('setProject',resp)
        }
    }
}