/**
 *  facthData 
 * @param {*} defaultData 
 * @returns 
 */
export default function (defaultData = []) {
    return {
        data() {
            return {
                // 
                isLoading: true,
                // 
                data: defaultData,
            }
        },

        async created() {
            this.data = await this.fatchData();
            this.isLoading = false;
            // console.log(this.data);
        },

    }
}