// 

// 
import App from "@/eventBus";
// 
import debounce from "@/untils/debounce";

// 
let imgArr = [];

// 
import imgSrc from "@/assets/imgLoading.gif";

// 
function setImageLoading(img) {
    if (!img.dom) {
        return;
    }
    // 
    img.dom.src = imgSrc;
    const clientHeight = document.body.clientHeight;
    const imgInfo = img.dom.getBoundingClientRect();
    // console.log(clientHeight)
    // 
    if (imgInfo.height + imgInfo.top <= clientHeight) {
        // 
        const tempImg = new Image();
        tempImg.src = img.src;
        tempImg.onload = function () {
            img.dom.src = tempImg.src;
        }
        imgArr = imgArr.filter(item => item !== img);
    }
}

// 
function setAllImage() {
    for (const item of imgArr) {
        setImageLoading(item);
    }
}

// 
function handlerScroll(dom) {
    setImageLoading(dom);
}

// 
App.$on('handleScroll',debounce(handlerScroll,50))

export default {
    //  bind 
    //  inserted 
    inserted(el,bingding) {
        const img = {
            dom: el,
            src: bingding.value,
        };
        imgArr.push(img);
        setAllImage();
    },
    unbind(el) {
        imgArr = imgArr.filter((item) => {
            return item.dom !== el;
        });
        // console.log(imgArr
    }
}